import React, {useState} from 'react';
import {Link} from 'react-router-dom';

import './Login.css';

const Login = () => {
    const [username, setUsername] = useState('');
    const [password, setPassword] = useState('');

     return (
    <div className="joinOuterContainer">
      <div className="joinInnerContainer">
        <h1 className="heading">Dryer Tracker</h1>
        <div>
          <input placeholder="username" className="joinInput" type="text" onChange={(event) => setUsername(event.target.value)} />
        </div>
        <div>
          <input placeholder="password" className="joinInput mt-20" type="text" onChange={(event) => setPassword(event.target.value)} />
        </div>
        <Link onClick={e => (!username || !password) ? e.preventDefault() : null} to={`/dryeradd`}>
          <button className={'button mt-20'} type="submit">Login</button>
        </Link>
      </div>
    </div>
  );
}

export default Login;