import React, { useState, useEffect } from 'react';
import Axios from 'axios';
import './Dryers.css'
import {Link} from 'react-router-dom';


 
const DryerList = ({list}) => {
  const [dryerList, setDryerList] = useState([]);
  const [newstatus, setNewStatus] = useState('');


 useEffect(() => {
    Axios.get("https://dryer-tracker-heroku.herokuapp.com/dryers")
    .then(response => setDryerList(response.data));
  },[]); 

  const updateStatus = (id) => {
    Axios.put("https://dryer-tracker-heroku.herokuapp.com/update", {status: newstatus, id: id}).then(
      (response) => {
        setDryerList(
          dryerList.map((val) => {
            return val.id == id
            ? {
              id: val.id,
              serialNumber: val.serialNumber,
              buildDate: val.buildDate,
              status: newstatus,
            } 
            : val;
          })
        );
      }
    );
  };
  
  const removeDryer = (id) => {
    Axios.delete(`https://dryer-tracker-heroku.herokuapp.com/delete/${id}`).then((response) => { setDryerList(dryerList.filter((val) => {
      return val.id != id
    }))
    })
  };
 
  return (
    <>
    
    <div className = "button">
      <div> <h4 className='modal'>Dryer List</h4></div>
       <Link to="/dryeradd">
          <button className='btn'>Back</button>
      </Link>
    </div>
    <div className="container">
      <ul>
        {dryerList.map((val, key) => {
          return <div className="container" key={key}>
              <h3>Serial Number: {val.serialNumber}</h3>
              <h3>Build Date: {(new Date(val.buildDate)).toLocaleDateString('en-GB')}</h3>
              <h3>Status: {val.status}</h3>
              <div>
                {""}
              <select className='btn'onChange={(e) => {setNewStatus(e.target.value)}}>
               <option value="">Select</option>                
               <option value="warehouse">Warehouse</option>
               <option value="shipped">Shipped</option>
               <option value="delivered">Delivered</option>
               <option value="installed">Installed</option>
               </select>
                <button className='btn'onClick={() => {
                  updateStatus(val.id)}}>Update Status</button>
                  <button className='btn' onClick={() => {removeDryer(val.id)}}>Remove</button>
                </div>
              </div>
          })}
          </ul>
    </div>
    </>
  );
};
  export default DryerList;