import React, { useState } from 'react';
import './Dryers.css'
import Axios from 'axios';
import {Link} from 'react-router-dom';
import {useForm} from 'react-hook-form';


const Dryers = () => {
  const [serialNumber, setSerialNumber] = useState('');
  const [buildDate, setBuildDate] = useState('');
  const [status, setStatus] = useState('');
  const [dryerList, setDryerList] = useState([]);

  

  const addDryer = (data,e) => {
    Axios.post("https://dryer-tracker-heroku.herokuapp.com/create", {
      serialNumber: serialNumber, 
      buildDate:buildDate, 
      status:status
    }).then((response) => {alert("Dryer has been added")},() => {
      setDryerList([...dryerList, {serialNumber: serialNumber, 
      buildDate:buildDate, 
      status:status}])
    });
    e.target.reset();
  }
 
  const {register, errors, handleSubmit = (e) => {e.preventDefault();}} = useForm();
 

   /* const handleSubmit = (e) => {
    e.preventDefault();} */
  return (
    <>
    <article>
      <div className = "button">
        <Link to="/">
          <button className='btn'>Logout</button>
          </Link>
      </div>
        <form className='form' onSubmit={handleSubmit(addDryer)}>
          <div className='form-control'>
            <label htmlFor='serialNumber'>Serial Number: </label>
            <input 
              type='text'
              id='serialNumber'
              name='serialNumber'
              ref={register({ 
                required: true, 
                minLength: 10,
                pattern: /^ECO\d{8}/i,
              })}
              placeholder='Enter a valid serial number starting with ECO'
              onChange={(e) => setSerialNumber(e.target.value)}
            />
            {errors.serialNumber && errors.serialNumber.type === "required" && ( <p>Please fill in the required field</p>
            )}
            {errors.serialNumber && errors.serialNumber.type === "minLength" && ( <p>This field requires min length of 10 characters</p>
            )}            
            {errors.serialNumber && <p>please enter a value in the format ECO01234567</p>}          
          </div>
          <div className='form-control'>
            <label htmlFor='buildDate'>Build Date: </label>
            <input
              type='date'
              id='buildDate'
              name='buildDate'
              ref={register({ required: true})}
              onChange={(e) => setBuildDate(e.target.value)}
            />
            {errors.buildDate && <p>Please select a date</p>}
          </div>
            <div className="form-control">
             <label htmlFor='status'>Status: </label>
               <select 
               type='select'
               id='status'
               name='status'
               className='status' 
               ref={register({ required: true})}
               onChange={(e) => {setStatus(e.target.value)}}>
               <option value="">Select</option>
               <option value="warehouse">Warehouse</option>
               <option value="shipped">Shipped</option>
               <option value="delivered">Delivered</option>
               <option value="installed">Installed</option>
               </select>
               {errors.status && <p>Please select the dryer status</p>}
          </div>
          <button className={'button mt-20'}onClick={handleSubmit}>add dryer</button>
          <Link to="DryerList">
          <button className={'button mt-20'}>View Dryer List</button>
          </Link>
        </form>
        {/* {dryerList.map((val, key) => {
            return <div className="dryerList">
              <h3>Serial Number: {val.serialNumber}</h3>
              <h3>Build Date: {val.buildDate}</h3>
              <h3>Status: {val.status}</h3>
              </div>
          })} */}
        </article>
    </>
  );
};

export default Dryers;